<template>
  <div class="container">
    <div class="mobile-title">
      <p>我的</p>
    </div>
    <div class="mobile-body">
      <p>修改密码</p>
      <van-button round type="danger" plain @click="quit">退出登录</van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Mine",
  methods: {
    quit () {
      debugger
      const userMassage = JSON.parse(window.localStorage.getItem('userMassage'))
      window.localStorage.setItem('userMassage', JSON.stringify(
        {
          name :userMassage.name,
          password:userMassage.password,
          checked: userMassage.checked,
          isLogin: false
        }
       ))
      this.$router.push({ name: 'Login', query: window.localStorage.getItem('userMassage') })
      // window.localStorage.clear()
    }
  }
}
</script>

