    padding-left: 1rem;
   p {
    font-size: 1.375rem;
    font-weight: 500;
   } 
  }
  .mobile-body {
    p {
      margin-top: 2.875rem;
      border-bottom: 1px solid #F3F3F3;
      padding-bottom: 0.6875rem;
    }
  }
  .van-button {
    width: 90%;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
